.form-control {
  margin-bottom: 1rem;
}

.form-control input,
.form-control label {
  display: block;
}

.form-control label {
  font-weight: bold;

  margin-bottom: 0.5rem;
}

.form-control input {
  font: inherit;

  padding: 0.5rem;

  border-radius: 4px;

  border: 1px solid #ccc;

  width: 20rem;
  max-width: 100%;
}

.form-control input:focus {
  outline: none;

  border-color: #240370;

  background-color: #e0d4fd;
}
