.app {
  width: 90%;
  max-width: 43rem;

  padding: 1rem;
  border-radius: 12px;
  margin: 3rem auto;

  background-color: white;
}

.control-group {
  display: flex;
  column-gap: 1rem;
  flex-wrap: wrap;
}

.control-group .form-control {
  min-width: 15rem;

  flex: 1;
}

button {
  font: inherit;

  background-color: #240370;
  color: white;

  border: 1px solid #240370;
  padding: 0.5rem 1.5rem;
  border-radius: 4px;

  cursor: pointer;
}

button:hover,
button:active {
  background-color: #33059e;
  border-color: #33059e;
}

button:disabled,
button:disabled:hover,
button:disabled:active {
  background-color: #ccc;
  color: #292929;
  border-color: #ccc;
  cursor: not-allowed;
}

.form-actions {
  text-align: right;
}

.form-actions button {
  margin-left: 1rem;
}

.invalid input {
  border: 1px solid #b40e0e;
  background-color: #fddddd;
}

.invalid input:focus {
  border-color: #ff8800;
  background-color: #fbe8d2;
}

.error-text {
  color: #b40e0e;
}
